import React, { Component } from "react";
import { Container, Grid, Typography } from "@material-ui/core";
import { access } from "fs";
import Avatar from '@material-ui/core/Avatar';

export default class Home extends Component {
    constructor() {
        super();
        this.state = {
            nome: "",
            user: {}
        }
    }

    componentDidMount() {
        if (window.location.search.length > 0) {
            if (window.location.search.split("?")[1].split("code=")[1] != undefined) {
                var code = window.location.search.split("?")[1].split("code=")[1]
                fetch("https://brunoeleodoro.api.stdlib.com/MyFirstServelessApp@dev/", {
                    method: 'POST',
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ my_code: code })
                }).then(results => {
                    return results.json();
                }).then((data) => {
                    console.log(JSON.parse(data.body))
                    var access_token = JSON.parse(data.body).access_token
                    this.getUserInfo(access_token);


                });
            }
        }
        else {
            window.location = "https://github.com/login/oauth/authorize?client_id=Iv1.bc3e680d4058764d"
        }

    }

    getUserInfo = (access_token) => {
        if (access_token == undefined) {
            window.location = "https://github.com/login/oauth/authorize?client_id=Iv1.bc3e680d4058764d"
            return;
        }
        fetch("https://api.github.com/user", {
            method: 'GET',
            headers: {
                'Authorization': 'token ' + access_token,
                'Content-Type': 'application/json'
            },
            // mode: 'no-cors'
            // dataType:"jsonp"
        }).then(results => {
            return results.json();
            // return results
        }).then((data) => {
            this.setState({ user: data })

        });
    }

    render() {
        var pageContent = ""
        if (this.state.user.avatar_url != undefined) {
            pageContent = (<div>
                <Avatar alt="Img" src={this.state.user.avatar_url} />
                <br />
                <Typography variant="body1" >{this.state.user.name} </Typography>
                <br />
                <Typography variant="body1" >{this.state.user.bio} </Typography>
            </div>)
        }
        return (
            <Container maxWidth="lg" maxWidth={"lg"} >
                <Grid
                    container
                    direction="row"
                    justify="center"
                    alignItems="center"
                >
                    {pageContent}

                </Grid>
            </Container>
        );
    }
}